.lbbLightBoxModal {
	width: 100%;
	height: 100vh;

	.components-modal__content {
		height: 100%;
		overflow-x: hidden;
		padding: 60px 0 0 0;
		margin: 0;
		margin-bottom: 300px;

		&::before {
			margin: 0;
		}
	}

	.lbbContentArea {
		height: 100%;
	}

	.lbbContentBox {
		width: 100%;
		height: 100%;
		display: flex;
		overflow: hidden;

		.lbbContent80 {
			flex: 1;
			overflow-y: auto;
			overflow-x: hidden;
			padding: 10px 10px 300px 10px;

			.popoverSlotOuter {
				.popover-slot {
					.components-popover {
						position: absolute;
					}
				}
			}
		}

		.lbbContent20 {
			width: 300px;
			overflow-y: auto;
			overflow-x: hidden;
		}
	}
}

html :where(.lbbContent80 .wp-block) {
	max-width: 100% !important;
}

.lbbLightBoxPlaceholder {
	text-align: center;
	background-color: #4527a411;
	padding: 20px;

	h3 {
		font-size: 20px;
		display: inline-flex;
		align-items: center;
		gap: 15px;
		margin: 20px 0;
		justify-content: center;

		.dashicons {
			width: 24px;
			height: 22px;
			font-size: 24px;
			color: #4527a4;
		}
	}

	.lightboxlayout {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		gap: 25px;
		padding: 7.5px;
		margin: 0 auto;

		button {
			color: #4527a4;
			background: #4527a422;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 6px 14px !important;
			border: none;
			border-radius: 3px;
			outline: 0;
			cursor: pointer;
			font-weight: 600;
			transition: 0.3s;

			&:hover {
				background: #4527a411;
			}
		}
	}
}

.lbbTabPanel {
	.lbbWHControl {
		display: flex;
		align-items: center;
		justify-content: center;
		column-gap: 20px;
		margin-top: 10px;
	}

	.components-input-control {
		height: 100%;
	}

	.lbbPanelBody {
		.components-input-control {
			height: 100%;
		}

		.components-button-group.bPlBtnGroup {
			display: flex;
			align-items: center;
		}
	}

	.components-unit-control__unit-label {
		height: 100%;
	}

	label,
	p {
		font-size: 13px;
	}

	button.removeAllitem {
		background-color: #fd0952a1;
		color: #fff;
		font-weight: 500;
		text-align: right;
		justify-content: center;
		width: 100%;
		transition: 0.3s;

		&:hover {
			color: #000;
			transition: 0.1s;
		}
	}
}

// editor css 
.lbbLightBox {
	.lbbContent_area {
		.edit-post-visual-editor.lbbmodalbtn a {
			transition: all 0.0s linear;
		}
	}
}

.components-modal__screen-overlay.lbbLightBoxModalOverlay {
	z-index: 1000;
}

.lbbPopProtect {
	width: 100%;
	height: 100%;
	position: absolute;
	z-index: 9;
}

